import goodsHeader from "./goods-Header.vue"
import interfaces from "../../unils/interfaces.js"
import specPop from "../../components/specPop.vue"
export default {
	components: {
		goodsHeader,
		specPop,
	},
	data() {
		return {
			isKeep: false, //收藏状态
			goodsdetail: {
				swiperList: [],
				spec: [],
				comment: [{
					face: "",
					content: "",
					username: ""
				}],
			},
			goodsinfo: {
				name: "",
				price: "",
				number: "1",
				"spec": ""
			},
			currentSwiper: 0, //轮播图下标
			showinfo: {
				showspec: false
			}
		}
	},
	onLoad(option) {
		if (option.goodsinfo) {
			this.goodsinfo = JSON.parse(option.goodsinfo)
			console.log("goodsinfo", this.goodsinfo)
		}
		this.initData()
	},
	methods: {
		add() {
			if (this.goodsinfo.number < 0) {
				return
			}
			this.goodsinfo.number++;
		},
		sub() {
			if (this.goodsinfo.number <= 1) {
				return
			}
			this.goodsinfo.number--
		},
		hideSpec() {
			this.showinfo.showspec = false;
		},
		setSpec(item) {
			this.goodsinfo.spec = item;
		},
		// 立即购买
		handleBuy() {
			let tempList = [];
			tempList.push(this.goodsinfo);
			uni.setStorage({
				key: "accountList",
				data: tempList,
				success: () => { //跳转结算页
					uni.navigateTo({
						url: "./goods-account"
					})
				}
			})

		},
		//加入购物车
		joinCart() {
			// console.log(this.goodsinfo)
			// 点击购物车,本地存储当前商品信息
			// 先取数据
			uni.getStorage({
				key: "cartList", //已经加入购物车商品列表
				success: (res => {
					//获取数据
					// console.log("添加成功")
					// console.log(res)

					let cartList = res.data;
					// 商品不存在
					let flag = false;

					cartList.forEach(item => {
						// 判断购物车是否存在当前商品id和规格
						if (item.goods_id == this.goodsinfo.goods_id && item.spec == this.goodsinfo.spec) {
							// 如果存在修改商品数量
							item.number += Number(this.goodsinfo.number);
							console.log("商品存在，修改数量", item.number)
							flag = true;
						}
					})
					if (!flag) { //不存在则加入购物车
						cartList.push(this.goodsinfo)
					}

					this.setcarList(cartList)

				}),
				fail: (err => {
					//点击加入购物车 存入数组
					// console.log("加入失败")
					let cartList = [];
					cartList.push(this.goodsinfo)

					//存入本地
					this.setcarList(cartList)
				})
			})
		},
		// 自定义本地存储方法
		setcarList(cartList) {
			uni.setStorage({
				key: "cartList",
				data: cartList,
				success: () => {
					uni.showToast({
						icon: "success",
						title: "添加成功"
					})
				}
			})
		},
		// 收藏
		keep() {
			this.isKeep = !this.isKeep;
			uni.showToast({
				title: this.isKeep ? "已收藏" : "已取消收藏"
			})
		},
		// 轮播图变化导致下标变化
		swipwerChange(e) {
			// console.log(e.target)
			this.currentSwiper = e.target.current;
		},
		initData() {
			this.request({
				url: interfaces.getGoodsDetail,
				success: ((res) => {
					this.goodsdetail = res.data;
					console.log("goodsdetail", this.goodsdetail);
				})
			})
		},
		showAll() {
			// 本地存储评论
			uni.setStorageSync("comments", this.goodsdetail.comment)
			// 跳转到评论列表组件
			uni.navigateTo({
				url: "./goods-comment"
			})
		}
	}
}
